.item {
    position: relative;
    display: flex;
    gap: 16px;
    align-items: center;
}

.statusOnline {
    position: absolute;
    bottom: 10%;
    left: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 8px;
    background-color: green;
}

.statusOffline {
    position: absolute;
    bottom: 10%;
    left: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 8px;
    background-color: red;
}

.avatarr {
    max-width: 30%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    background-color: hsla(215, 98%, 79%, 0.3);
    box-shadow: 2px 2px 5px #babecc, -5px -5px 10px #fff;
    margin-bottom: 10px;
    margin-top: 10px;
}

.name {
    font-size: 24px;
    font-weight: bold;
}